.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    z-index: 1;
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .backdrop.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
  
  .backdrop.is-hidden .information {
    transform: translate(-50%, 50%) scale(0);
  }
  
  .information {
    position: absolute;
    padding: 30px;
    margin: 15px;
    max-width: 500px;
    width: 100%;
    /*min-height: 609px; */
    background-color: #e17992;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    @media screen and (min-width: 1200px) {
      max-width: 528px;
    }
  
    &__text {
      font-weight: 500;
      font-size: 12px;
      line-height: 1.58;
      text-align: initial;
      letter-spacing: .04em;
      color: #fff;
    }
  
    &__closingbutton {
      width: 176px;
      height: 44px;
      margin: 20px auto 0 auto;
      font-weight: 700;
      font-size: 14px;
      line-height: 1.28;
      fill: #d41443;
      color: #d41443;
      background: #fff;
      border-radius: 22px;
      transition: color .25s cubic-bezier(.4,0,.2,1),background-color .25s cubic-bezier(.4,0,.2,1);
      background-color: #fff;
      border: 1px solid rgba(0,0,0,.1);
      display: block;
      &:hover {
        background-color: #ecebeb;
        color: #d41443;
      }
    }
  }